function BookNotes() {
  return (
    <>
      <div id="Viewbook-notes">
        <div>
          <label className="inp-text">Notes</label>
          <textarea
            addbookinp=""
            type="text"
            name="name"
            placeholder="Some important detail about this book"
            id=""
          />
        </div>

        <div>
          <div>
            <span>Bookmarks</span> <span>^</span>
          </div>
          <div>
            <div>Add bookmark</div>
          </div>
        </div>
      </div>
    </>
  );
}

export default BookNotes;
