import React,{useEffect,useContext,useState} from 'react'
import './ViewBook.css'
import { api_url } from '../..'
import {
  useParams
} from 'react-router-dom'

import { textareaElemUI } from '../addbook/AddBook'
import AuthContext from '../../context/AuthContext'
import Swal from 'sweetalert2'
import { useNavigate } from 'react-router-dom'
import { Link,Routes,Route,NavLink } from 'react-router-dom'
import BookNotes from './BookNotes'
import BookDetails from './BookDetails'

// import withReactContent from 'sweetalert2-react-content'



function ViewBook(props){
  let { authTokens } = useContext(AuthContext);
    let { slug } = useParams()
    let [fetchedData,setFetchedData] = useState("")
    let navigate = useNavigate()


    const DeleteBookreq= async (e)=> {
      // console.log(e.target);
      // const MySwal = withReactContent(Swal)
      // await MySwal.fire({
      //   title: <strong>Book Read Pages Updated</strong>,
      //   // html: <i>Click Login Button to Login</i>,
      //   icon: 'success',
      //   denyButtonText: <p className='boldwhitep'  > Great </p>
      // })
      Swal.fire({
        title: 'Are you sure! you are about to delete this book?',
        // showDenyButton: true,
        showCancelButton: true,
        confirmButtonText: 'Yes,Delete it',
        denyButtonText: 'No',
        icon:'warning',
        customClass: {
          actions: 'my-actions',
          cancelButton: 'order-1 right-gap',
          confirmButton: 'order-2',
          denyButton: 'order-3',
        }
      }).then((result) => {
        if (result.isConfirmed) {
          fetch(`${api_url}api/delete_book/`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: "Bearer " + String(authTokens.access),
            },body:JSON.stringify({
              "book_id":slug
            })
          }).then(resp => resp.json())
          .then(data => {
            navigate('/')
            console.log(`#o${slug}`,document.querySelector("[mylibrarybody]"))
            document.querySelector("[mylibrarybody]").querySelector(`#o${slug}`).remove()
          Swal.fire('Book Deleted', '', 'success');})


        } 
      })

      // let response = await fetch(`${api_url}api/usernameavailibility/`, {
      //     method:'POST',
      //     headers:{ 
      //         'Content-Type':'application/json',
      //         // 'Authorization':'Bearer ' + String(authTokens.access)
      //     },
          
      //     body:JSON.stringify({username:e.target.value})
      // })
      // let data = await response.json()
      // console.log(data)
      // console.log(data.available)
  
      
      
      
    }

    const UpdateBookreq= async (e)=> {
      e.preventDefault()
      Swal.fire({
        title: 'Are you sure to make changes to this book?',
        // showDenyButton: true,
        showCancelButton: true,
        confirmButtonText: 'Yes',
        icon:'warning',
        
      }).then((result) => {
        if (result.isConfirmed) {
          console.log(e.target.name.value)
          fetch(`${api_url}api/update_book/`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: "Bearer " + String(authTokens.access),
            },body:JSON.stringify({
              "book_ID":slug,
              "name":document.querySelector("#ViewBook-div").querySelector("[name=name]").value,
              "author":document.querySelector("#ViewBook-div").querySelector("[name=author]").value,
              "about":document.querySelector("#ViewBook-div").querySelector("[name=about]").value,
              "total_pages":document.querySelector("#ViewBook-div").querySelector("[name=total_pages]").value,
              "pages_read":document.querySelector("#ViewBook-div").querySelector("[name=pages_read]").value,
              "cover_page_url":document.querySelector("#ViewBook-div").querySelector("[name=coverpage_url]").value,
              "online_read_url":document.querySelector("#ViewBook-div").querySelector("[name=online_read_url]").value,
              "read_completed_times":document.querySelector("#ViewBook-div").querySelector("[name=read_completed_times]").value
            })
          }).then(resp => resp.json())
          .then(data => {
            if(data.success === 'true'){
            Swal.fire({
              title: 'Book Updated',
              confirmButtonText: 'Great',
              icon:'success',
              
            }).then((result) => {
              if (result.isConfirmed) {
                window.location.href = "/Bookworms";
      
      
              } 
            })} else{
              Swal.fire({
                title: 'Problem Updating Book',
                confirmButtonText: 'ok',
                icon:'error',
                
              })
            }
            })

        } 
      })

      
      
      
      
    }
  
    

    useEffect(() => {
      fetch(`${api_url}api/viewbook/${slug}/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + String(authTokens.access),
        },
      }).then(resp => resp.json())
      .then(data => {setFetchedData(data.data);
        textareaElemUI()});
     
    // console.log(fetcheddata)
      

    },[] )

    

    return (
    


        <>
          <form id="ViewBook-div" onSubmit={UpdateBookreq} >
            <div className='nlrdr' >

             <NavLink className={(navData) => (navData.isActive ? "active rdr" : 'rdr')} to={`details`}  > Details </NavLink>
            <NavLink className={(navData) => (navData.isActive ? "active rdr" : 'rdr')} to={`bookmarks`} > Notes </NavLink>
            </div>

          <Routes>

          <Route path={`/details`}  element={<BookDetails fetchedData={fetchedData} />} ></Route>
          <Route path={`/bookmarks`} element={<BookNotes/>} ></Route>

          </Routes>
          {/* <BookDetails fetchedData={fetchedData} /> */}

          <div id='viewbook-btns' >
          <Routes>

          <Route path={`/details`}  element={<button type='submit'    id="updateBook-btn" >Update Details</button>} ></Route>
          <Route path={`/bookmarks`} element={<button type='submit'    id="updateBook-btn" >Update Notes</button>} ></Route>

          </Routes>
          {/* <button type='submit'    id="AddBook-btn" >Update Book</button> */}
          <button type='button' onClick={DeleteBookreq}   id="DeleteBook-btn" >Delete Book</button>
          </div>
          </form>
        </>
      );    

}

export default ViewBook;